button{
  color: white;
  font-weight: 600;
  float: right;
  border-radius: 5px;
  border: none;
  background-color: #6699ff;
  padding: 10px 10px;
}
button:hover{
  -webkit-transition: 200ms;
  -o-transition: 200ms;
  transition: 200ms;
 scale:1.03;
 background-color: rgb(59, 143, 211);
}

.column1{
  height: 100%;
  float: left;
  width: 90%;
}

.column1 h2{
  color:#66ffff;
}
.column2{
  height: 100%;
  float: left;
  width: 10%;
}
.row{
  height: 50px;
  padding: 20px 50px 0px 30px ;
}

.box{
  background-color: #d966ff;
  border-radius: 0 0 10px 10px;
}

.box2{
  margin-left: 100px;
  color: #b30047;
}

.avatar{
  border-radius: 50%;
}


.column3{
  float: left !important;
  width: 33% !important;
}
h2{
  margin-top: 0px;
  padding-top: 0px;
}


.profile {
  border: 2px solid rgb(255, 166, 255);
  -webkit-box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%);
          box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%);
  max-width: 200px;
  margin: 10px 5px;
  text-align: center;
  font-family: arial;
  padding: 15px;
  border-radius: 18px;
  background-color: #ffcc99;
}

.profile:hover{
  -webkit-transition:400ms;
  -o-transition:400ms;
  transition:400ms;
  -webkit-box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
          box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  -webkit-transform: translateY(-3px);
      -ms-transform: translateY(-3px);
          transform: translateY(-3px);
}
.email {
  color: rgb(59, 52, 52);
  font-size: 18px;
  overflow-wrap: anywhere;
}
.name{
  font-size:25px;
  font-weight: bold;
}
.loader{
  margin-left: 300px;
  margin-top: 100px;
  margin-right: 80px;
}


